				haveNum:6666,//剩余个数
				unitPrice:'',//果实单价
				type:'1',
				wx:true,
				ali:true,
				list: [],
				loadingType: 0,
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				}
			};
		},
		onLoad(options) {
// 			uni.setNavigationBarColor({
// 				frontColor: '#ffffff',
// 				backgroundColor: '#12C2E9',
// 				animation: {
// 					duration: 400,
