// 						title: '请输入要卖出的数量!',
// 						duration:2000,
// 						icon:'none'
// 					});
// 					return;
// 				}
// 			},
			// 显示弹窗
			buyNow(e){
				this.toast=true
			},
			// 关闭弹窗
			toastHide(){
				this.toast=false;
			},
			// 选择支付方式
			pay(e){
				this.type = e.currentTarget.dataset.type;
			},
		},
		onLoad() {
			let list = [];
			for (let i = 1; i < 11; i++) {
				list.push(i);
			}
			this.list = list;
		},
		onReachBottom() {
			if (this.loadingType !== 0) {
				return;
			}
			this.loadingType = 1;
			let list = [],
				maxItem = this.list[this.list.length - 1],
				length = maxItem +10;
			for (let i = maxItem + 1; i < length; i++) {
				list.push(i);
			}
			setTimeout(() => {
				if (length > 26) {
					this.loadingType = 2;
					return;
				}
				this.list = this.list.concat(list);
				this.loadingType = 0;
			}, 1500);
		},
		components: {
			loadMore
		}
	}



